* {
  --main-color: #00acdc;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.navigation {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  white-space: nowrap;
  color: #000;
  align-items: center;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

a:hover {
  text-transform: uppercase;
  color: var(--main-color);
}

.is-active, .active {
  color: var(--main-color);
}

.is-active:after {
  background-color: var(--main-color);
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  top: 56px;
  bottom: 0;
  left: 0;
}
